window.dom={
    //ÂàõÂª∫ËäÇÁÇπÁöÑ‰∏âÁßçÊñπÂºè
    //Á¨¨‰∏ÄÁßç‰∏çËÉΩÂä†<span><div>hi</div></span>ËøôÁßçÊ†ºÂºè
    create1(tagName){
       return document.createElement(tagName);
    },
    //Á¨¨‰∫åÁßç‰∏çÊîØÊåÅ<tr><td></td></tr>
    create2(string){
        const container = document.createElement("div");
        container.innerHTML =string.trim();
        return container.children[0];

    },
    //templateÂèØ‰ª•ÂÆπÁ∫≥tr„ÄÅtdÊ†áÁ≠æ
    create3(string){
        const container = document.createElement("template");
        container.innerHTML =string.trim();
        return container.content.firstChild;

    },

    //Êñ∞Â¢ûÂºüÂºü(ÊääÂàõÂª∫ÁöÑËäÇÁÇπÊ∑ªÂä†Âà∞Â∑≤ÊúâËäÇÁÇπÂêéÈù¢ÔºåÊ∏≤ÊüìÈ°µÈù¢)
    after(node,node2){
        node.parentNode.insertBefore(node2,node.nextSibling);
    },
    //Êñ∞Â¢ûÂì•Âì•
    before(node,node2){
        node.parentNode.insertBefore(node2,node);
    },
    //Êñ∞Â¢ûÂÑøÂ≠ê
    append(parent,node){
        parent.appendChild(node);
    },
    //Êñ∞Â¢ûÁà∏Áà∏
    wrap(node,parent){
        //Text,div4
        dom.before(node,parent);//ÂøÖÈ°ªÊúâËøôÂè•
        dom.append(parent,node);
    },
    //Âà†Èô§ËäÇÁÇπ
    remove(node){
        node.parentNode.removeChild(node);//ËäÇÁÇπÊ≤°ÊúâÁà∂ËäÇÁÇπÁöÑÊÉÖÂÜµ‰∏ã‰ºöÊä•Èîô
        //node.remove();‰πüÂèØ‰ª•Ôºå‰ΩÜÊòØÂèØËÉΩ‰∏çÊîØÊåÅIE
        return node;
    },
    //ÁßªÈô§ÊâÄÊúâÁöÑÂ≠êËäÇÁÇπÔºåÂåÖÊã¨ÊñáÊú¨Â≠êËäÇÁÇπÂíåÂÖÉÁ¥†Â≠êËäÇÁÇπ
    empty(node){
        const array = [];
        let x = node.firstChild;
        while(x){
            array.push(dom.remove(x));
            x=node.firstChild
        }
        return array;
    },
    //ÁªôÂÖÉÁ¥†Ê∑ªÂä†Â±ûÊÄßÔºåÊàñËÄÖËØªÂèñÂÖÉÁ¥†ÁöÑÂ±ûÊÄß
    attr(node,name,value){//ÈáçËΩΩ
        if(arguments.length === 3){
            node.setAttribute(name,value);
        }else if(arguments.length === 2){
            return node.getAttribute(name);
        }
    },
    //ËØªÂÜôÊñáÊú¨ÂÜÖÂÆπ
    text(node,string){//ÈÄÇÈÖç-ÈÄÇÈÖç‰∏çÂêåÊµèËßàÂô®
        if(arguments.length===2){
            if('innerText' in node){
                node.innerText = string;//ÈÉ®ÂàÜIEÂè™ÊîØÊåÅinnerText
            }else{
                node.textContent = string;//Ë∞∑Ê≠åÂíåÁÅ´ÁãêÊîØÊåÅ
            }
        }else if(arguments.length===1){
            if('innerText' in node){
                return node.innerText;//ÈÉ®ÂàÜIEÂè™ÊîØÊåÅinnerText
            }else{
                return node.textContent;//Ë∞∑Ê≠åÂíåÁÅ´ÁãêÊîØÊåÅ
            }
        }
        
    },
    //ËØªÂÜôhtmlÂÜÖÂÆπ
    html(node,string){
        if(arguments.length ===2){
            node.innerHTML = string;
        }else if(arguments.length ===1){
            return node.innerHTML;
        }
    },
    //‰øÆÊîπstyle
    style(node,name,value){
        if(arguments.length ===3){
            //dom.style(div,'color','red')
            node.style[name] =value;
        }else if(arguments.length ===2){
            if(typeof name === 'string'){
                //dom.style(div,'color')
                return node.style[name];
            }else if(name instanceof Object){
                //dom.style(div,{border:1px solide red,'color':'blue'})
                const object = name;
                for(let key in object){
                    //console.log(node.style);
                    node.style[key]=object[key];
                }
            }
        }
    },
    //Â¢ûÂà†Êü•class
    class:{
        add(node,className){
            node.classList.add(className);
        },
        remove(node,className){
            node.classList.remove(className);
        },
        has(node,className){
           return node.classList.contains(className);
        }
    },
    //Ê∑ªÂä†ÁõëÂê¨‰∫ã‰ª∂
    on(node,eventName,fn){
        node.addEventListener(eventName,fn);
    },
    //Âà†Èô§ÁõëÂê¨‰∫ã‰ª∂
    off(node,eventName,fn){
        node.removeEventListener(eventName,fn);
    },
    //Ëé∑ÂèñÊ†áÁ≠æÊàñÊ†áÁ≠æ‰ª¨
    find(selector,scope){
        return (scope||document).querySelectorAll(selector);
    },
    //Ëé∑ÂèñÁà∂Ê†áÁ≠æ
    parent(node){
        return node.parentNode;
    },
    //Ëé∑ÂèñÂ≠êÊ†áÁ≠æ
    children(node){
        return node.children;
    },
    //Ëé∑ÂèñÂÖÑÂºüÂßêÂ¶πÂÖÉÁ¥†
    sibling(node){
        return Array.from(node.parentNode.children)
        .filter(n=>n!=node)
    },
    //Ëé∑ÂèñÂºüÂºü
    next(node){
        let x = node.nextSibling;//‰∏ã‰∏Ä‰∏™ËäÇÁÇπÂèØËÉΩÊòØÊñáÊú¨ËäÇÁÇπ
        while(x && x.nodeType ===3){
            x=x.nextSibling;
        }
        return x;
    },
    //Ëé∑ÂèñÂì•Âì•
    previous(node){
        let x = node.previousSibling;//‰∏ã‰∏Ä‰∏™ËäÇÁÇπÂèØËÉΩÊòØÊñáÊú¨ËäÇÁÇπ
        while(x && x.nodeType ===3){
            x=x.previousSibling;
        }
        return x;
    },
    //ÈÅçÂéÜÊâÄÊúâËäÇÁÇπ
    each(nodeList,fn){
        for(let i=0;i<nodeList.length;i++){
            fn.call(null,nodeList[i]);
        }

    },
    //Ëé∑ÂèñÊéíË°åËÄÅÂá†
    index(node){
        const list = dom.children(node.parentNode);
        let i;
        for(i=0;i<list.length;i++){
            if(list[i]===node){
                break;
            }
        }
        return i;
    }
}